@charset "UTF-8";
.brands {
  background: #F2F2F2; }

.brands__title {
  width: auto;
  /* на больших размерах высота меньше чем на 320*/
  height: 80px;
  font-family: "TT Lakes", sans-serif;
  color: #7E7E82;
  font-weight: 400;
  font-size: 16px;
  padding: 16px 16px 0;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-transform: uppercase;
  /*background: #FFFFFF;*/
  background-image: linear-gradient(to right, #F2F2F2 30%, #FFFFFF);
  border-top: 1px solid #D9FFF5; }

.brands__list {
  /*display: -webkit-box;*/
  /*display: -ms-flexbox;*/
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /*overflow: scroll;*/
  padding-top: 16px;
  padding-bottom: 35px; }

.brands__slider-item {
  position: relative;
  background: #FFFFFF;
  border-radius: 7px;
  margin-right: 16px;
  cursor: pointer;
  min-width: 240px;
  min-height: 70px;
  max-height: 70px; }

.brands__slider-item:hover {
  outline: 2px solid #FF3E79; }

.circle-logo {
  position: absolute;
  top: 16px;
  right: 16px; }

.dropdown-logo {
  position: absolute;
  top: 31px;
  right: 30px; }

.swiper-slide:first-child {
  margin-left: 16px; }

.img-logo-lenovo {
  margin: 20px 16px 0; }

.img-logo-sumsung {
  margin: 17px 16px 0; }

.img-logo-apple {
  margin: 12px 16px; }

.img-logo-view {
  margin: 26px 14px 25px; }

.img-logo-bosh {
  margin: 21.7px 16px; }

.img-logo-hp {
  margin: 10px; }

.img-logo-acer {
  margin: 24px 16px; }

.img-logo-sony {
  margin: 23.4px 14px; }

.swiper-pagination {
  /*position: absolute;*/
  top: 94px; }

.swiper__list {
  display: flex;
  justify-content: space-between;
  margin-left: 38px;
  margin-right: 38px; }

.brands_hide-button {
  display: none; }

.brands_show-button {
  display: none; }

.brands__name-list {
  display: none; }

@media screen and (min-width: 768px) {
  .brands__main-container {
    display: flex;
    flex-wrap: wrap;
    max-width: 768px;
    height: 200px;
    overflow-y: hidden; }
  .brands__slider {
    display: none; }
  .brands__title {
    height: 56px;
    padding-left: 24px; }
  .brands__name-list {
    display: flex;
    flex-direction: row;
    /*justify-content: ;*/
    flex-wrap: wrap;
    /*width: 200px;*/
    /*overflow: hidden;*/
    /*height: 200px;*/ }
  .brands__item:hover {
    outline: 2px solid #FF3E79; }
  .brands__item {
    display: block;
    border-radius: 7px;
    position: relative;
    margin-top: 10px;
    margin-bottom: 16px;
    margin-left: 24px;
    background: #FFFFFF;
    width: 230px;
    height: 70px; }
  .circle-logo {
    position: absolute; }
  .brands_show-button {
    display: block;
    color: #1B1C21;
    font-family: "TT Lakes", sans-serif; }
  .brands_show-button::before {
    content: url(./static/icon.svg);
    margin-left: 24px;
    margin-right: 17px; }
  .brands_hide-button::before {
    content: url(./static/icon.svg);
    margin-left: 24px;
    margin-right: 17px; }
  .brands_hide-button {
    display: none;
    color: #1B1C21;
    font-family: "TT Lakes", sans-serif; } }

@media screen and (min-width: 1120px) {
  .brands__main-container {
    display: flex;
    flex-wrap: nowrap;
    max-width: 1120px;
    height: 200px;
    overflow-y: hidden; }
  .for-hide {
    display: block; }
  .hidden {
    display: none; } }

@media screen and (min-width: 1400px) {
  .brands__main-container {
    display: flex;
    flex-wrap: nowrap;
    max-width: 1120px;
    height: 200px;
    overflow-y: hidden; } }

