.container {
  width: auto;
  /*height: 100%;*/ }
  .container__logo-menu {
    display: flex;
    position: relative;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 18px 20px 17px 16px;
    margin-left: 10px; }

.logo-menu__cps-logo {
  position: absolute;
  left: 88px; }

.logo-menu__call {
  margin-left: 290px; }

.content {
  position: relative; }
  .content__button-repair {
    position: absolute;
    right: 78px; }

.logo-menu__call,
.logo-menu__chat,
.logo-menu__profile {
  display: none; }

.logo-menu__cps-logo::after {
  position: absolute;
  content: "";
  height: 34px;
  width: 2px;
  background-color: #EAEAEA;
  display: inline-block;
  margin-left: 12px;
  margin-right: 4px;
  margin-top: 13px; }

@-webkit-keyframes animation {
  to {
    transform: scale(1.1);
    color: darkred; } }

@keyframes animation {
  to {
    transform: scale(1.1);
    color: darkred; } }

.logo-menu__burger:hover {
  filter: saturate(200%);
  -webkit-animation-name: animation;
          animation-name: animation;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate; }

