.wrapper {
  width: 320px;
  height: 100%;
  box-shadow: 7px 0 8px 0 rgba(34, 60, 80, 0.2);
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2; }
  @media screen and (min-width: 320px) {
    .wrapper {
      display: none; } }
  @media screen and (min-width: 1120px) {
    .wrapper {
      display: block; } }

.container__link:focus {
  color: #7E7E82;
  position: relative; }

.container__link:focus::after {
  content: "";
  position: absolute;
  top: -5px;
  left: -28px;
  width: 4px;
  height: 32px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  background-color: #41F6D7; }

.header {
  padding-top: 19px;
  display: flex;
  background: url(./static/cps-logo.svg) no-repeat 40% 60%;
  margin-top: 18px;
  justify-content: space-between; }

@-webkit-keyframes search {
  to {
    transform: scale(1.2); } }

@keyframes search {
  to {
    transform: scale(1.2); } }

@-webkit-keyframes close {
  to {
    transform: scale(1.1); } }

@keyframes close {
  to {
    transform: scale(1.1); } }

.header__search-button:hover {
  filter: saturate(200%);
  -webkit-animation-name: search;
          animation-name: search;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate; }

.header__close-button:hover {
  filter: saturate(200%);
  -webkit-animation-name: close;
          animation-name: close;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate; }

.header__close-button {
  margin-left: 20px;
  width: 40px;
  height: 40px; }

.header__search-button {
  margin-right: 24px;
  width: 40px;
  height: 40px; }

.container__list {
  padding-left: 30px;
  margin-top: 50px; }

.container__side-bar {
  display: flex;
  flex-direction: column; }

.container__list-item {
  padding-bottom: 25px; }

.social {
