.feedback {
  display: flex;
  flex-direction: column;
  padding-left: 24px; }

.feedback__email, .feedback__tel {
  padding-top: 16px; }

.feedback__tel {
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: 0.6px; }

.feedback__lang {
  font-size: 16px;
  line-height: 24px;
