.modal-feedback {
  visibility: hidden;
  position: fixed;
  z-index: 2;
  top: 0;
  right: 0;
  width: 320px;
  height: 732px;
  background-color: #FFFFFF; }
  @media screen and (min-width: 768px) {
    .modal-feedback {
      width: 455px;
      border-left: 1px solid #B8FFEC; } }
  .modal-feedback__content {
    position: relative;
    margin-left: 16px; }
    @media screen and (min-width: 768px) {
      .modal-feedback__content {
        margin-left: 32px; } }
  .modal-feedback__title {
    font-size: 24px;
    line-height: 32px;
    margin-top: 104px;
    margin-bottom: 10px;
    position: relative; }
    .modal-feedback__title::before {
      content: "";
      position: absolute;
      top: -11%;
      left: -16px;
      width: 4px;
      height: 40px;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
      background-color: #41F6D7; }
      @media screen and (min-width: 768px) {
        .modal-feedback__title::before {
          left: -32px; } }
    @media screen and (min-width: 768px) {
      .modal-feedback__title {
        margin-top: 40px; } }
  .modal-feedback__close-button {
    margin-top: 24px;
    margin-bottom: 40px;
    margin-left: 30px;
    position: absolute; }
    @media screen and (min-width: 768px) {
      .modal-feedback__close-button {
        margin-left: -50px;
        margin-top: 40px; } }
  .modal-feedback__name, .modal-feedback__email, .modal-feedback__phone, .modal-feedback__message {
    width: 288px;
    height: 48px;
    border: 1px solid #EAEAEA;
    border-radius: 10px 10px; }
    @media screen and (min-width: 768px) {
      .modal-feedback__name, .modal-feedback__email, .modal-feedback__phone, .modal-feedback__message {
        width: 376px; } }
  .modal-feedback__phone {
    margin-bottom: 16px;
    margin-top: 16px; }
  .modal-feedback__message {
    height: 118px;
    margin-top: 16px;
    margin-bottom: 16px; }
  .modal-feedback__text {
    width: 288px;
    color: #7E7E82;
    font-size: 14px;
    line-height: 16px; }
    .modal-feedback__text--pink {
      color: #FF3E79; }
  .modal-feedback__send-button {
    margin-top: 19px;
    margin-left: 154px; }
    @media screen and (min-width: 768px) {
      .modal-feedback__send-button {
        margin-left: 250px; } }
