.repair-content {
  background-color: #F8F8F8;
  padding-bottom: 20px; }
  @media screen and (min-width: 768px) {
    .repair-content__slides {
      display: none; } }
  .repair-content__title {
    font-weight: 400;
    font-size: 16px;
    color: #7E7E82;
    text-transform: uppercase;
    height: 80px;
    padding: 16px 16px 0;
    line-height: 24px;
    letter-spacing: -0.2px;
    background-image: linear-gradient(to right, #F2F2F2 30%, #FFFFFF); }
  .repair-content__swiper-item {
    min-width: 225px;
    min-height: 160px;
    margin: 10px;
    background-color: #FFFFFF;
    position: relative;
    border-radius: 7px; }
  .repair-content__text {
    margin-left: 16px; }
  .repair-content__icon {
    margin-right: 10px; }
  .repair-content__show-button, .repair-content__hide-button {
    display: none; }
  @media screen and (min-width: 768px) {
    .repair-content__show-button {
      display: block;
      margin-left: 20px;
      margin-top: 20px;
      padding-right: 20px; }
    .repair-content__hide-button {
      margin-left: 20px;
      margin-top: 20px;
      padding-right: 20px; } }
  .repair-content__pagination {
    top: 275px;
    padding-bottom: 10px;
    position: absolute; }
    @media screen and (min-width: 768px) {
      .repair-content__pagination {
        display: none; } }
  .repair-content__technical-list {
    display: none; }
    @media screen and (min-width: 768px) {
      .repair-content__technical-list {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        flex-wrap: wrap;
        height: 190px;
        overflow-y: hidden; } }
  .repair-content__item {
    min-width: 224px;
    min-height: 160px;
    margin-left: 16px;
    margin-right: 16px;
    margin-top: 16px;
    background-color: #FFFFFF;
    border-radius: 7px;
    position: relative; }
    .repair-content__item__item:first-child {
      margin-left: 25px; }
    .repair-content__item .repair-content__technical-circle {
      position: absolute;
      top: 100px;
      right: 17px; }
  .repair-content__circle {
    position: absolute;
    top: 106px;
    left: 170px; }

.showTechnicalList {
  height: 350px; }

.hide {
  display: none; }

.active {
  display: block; }
