.prices {
  background-color: #F8F8F8; }
  @media screen and (min-width: 768px) {
    .prices .prices__swiper {
      display: none; } }
  .prices__title {
    font-weight: 400;
    font-size: 16px;
    color: #7E7E82;
    text-transform: uppercase;
    height: 56px;
    padding: 16px 16px 0;
    line-height: 24px;
    letter-spacing: -0.2px;
    background-image: linear-gradient(to right, #F8F8F8 30%, #FFFFFF); }
  .prices__slide {
    min-height: 150px;
    background-color: #FFFFFF;
    margin-left: 16px;
    margin-right: 16px;
    margin-top: 10px;
    border-radius: 7px;
    position: relative; }
  .prices__slider-table {
    padding: 15px; }
  .prices__slider-title {
    font-size: 14px;
    line-height: 16px;
    color: #7E7E82;
    letter-spacing: 0.2px; }
  .prices__slider-description {
    color: #1B1C21;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px; }
  .prices__btn {
    position: absolute;
    left: 116px;
    top: 106px; }
  .prices__pagination {
    position: absolute;
    top: 180px;
    height: 10px;
    width: 100px; }
  .prices__table {
    width: 100%;
    height: 324px;
    background-color: #FFFFFF;
    padding-right: 20px;
    padding-left: 20px;
    font-size: 14px;
    color: #1B1C21; }
  .prices__items {
    display: none; }
    @media screen and (min-width: 768px) {
      .prices__items {
        display: flex;
        margin-left: 20px;
        margin-right: 20px; } }
  .prices__table-title {
    padding-left: 20px; }
  .prices__names {
    font-size: 14px;
    font-weight: 400;
    color: #7E7E82; }
  .prices__text {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    margin-top: 20px;
    margin-left: 20px;
    width: 40%; }
  .prices__link {
    position: relative;
    margin-top: 20px;
    font-weight: 500; }
    .prices__link::after {
      position: absolute;
      content: url(./static/more.svg); }

@media screen and (min-width: 320px) {
  .prices__text {
    width: 80%; } }
